@import "./mixin/_baseImport.scss";
@import "./_sprite/club_house_sing_along_sprite";
@import "./sprite/club_house_reader_num-sprite";
@import "./sprite/club_house_reader_num_active-sprite";
@import "./sprite/club_house_sing_along_bgitem1-sprite";



#wrap{
	&.club-house-sing-along{
		.wrap-outer{background: url("../../images/common/content_bg_inner01.png") no-repeat 50% -20px;
			.wrap-inner{background: url("../../images/club_house_sing_along/bg_club_house_sing_along01.jpg") no-repeat 0 0;}
		}
		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			.swiper{padding: 160px 100px 0;height: 768px;position: relative;box-sizing:border-box;
				&-container{width: 826px;}
				&-wrapper{}
				&-controller{
					.btn-swiper-prev{position: absolute;top: 325px;left: 15px;width: 44px;height: 72px;background: url("../../images/club_house_sing_along/btn_prev01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;
						&.swiper-button-disabled{opacity: 0;}
					}
					.btn-swiper-next{position: absolute;top: 325px;right: 15px;width: 44px;height: 72px;background: url("../../images/club_house_sing_along/btn_next01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;
						&.swiper-button-disabled{opacity: 0;}
					}
				}
				&-pagination {bottom: 46px;left: 0;width: 100%;display: flex;align-content: center;justify-content: center;flex-wrap: wrap;
					&-bullet{width: 39px;height: 45px;opacity: 1;transition: all 0.3s;margin: 0 6px;
						@for $i from 1 through 7 {
							&#{$i}{background: url("../../images/club_house_sing_along/icon_paging_num#{$i}.png") no-repeat 0 0;
								&.swiper-pagination-bullet-active{background: url("../../images/club_house_sing_along/icon_paging_num#{$i}_active.png") no-repeat 0 0;}
							}
						}
					}
				}
				&-slide{padding: 22px 0 14px;}
			}
			.clubhouse-sing-along{
				&__area{}
				&__box{}
				&__bg{}
				&__items{width: 826px;
					&.type1{display: flex;align-content: center;justify-content: center;flex-wrap: wrap;
						.clubhouse-sing-along__item{
							&:nth-of-type(1){margin: 0 56px;}
							&:nth-of-type(2){margin: 0 56px;}
							&:nth-of-type(3){margin: 27px 56px 0;}
							&:nth-of-type(4){margin: 27px 56px 0;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.type2{display: flex;align-content: center;justify-content: center;flex-wrap: wrap;
						.clubhouse-sing-along__item{
							&:nth-of-type(1){margin-right: 68px;}
							&:nth-of-type(2){margin-right: 68px;}
							&:nth-of-type(3){margin-right: 40px;}
							&:nth-of-type(4){margin: 27px 87px 0 0;}
							&:nth-of-type(5){margin: 27px 0 0;}
						}
					}
				}
				&__item{position: relative;width: 205px;height: 226px;display: block;transform: scale(1);transition: transform 0.3s;
					&.active{
						&:not(.disabled){transform: scale(1.05);
							&.bg-color01{&:before{right: -42px;}}
							&.bg-color02{&:before{right: -40px;}}
							&.bg-color03{&:before{right: -44px;}}
							&.bg-color04{&:before{right: -50px;}}
							&.bg-color05{&:before{right: -44px;}}
						}
					}
					&:before{content:"";position: absolute;top: 0;z-index: -1;transform: scale(1);}
					&.bg-color01{
						&:before{right: -18px;@include sprite($club_house_sing_along_bgitem1-bg_item01_02);transition: right 0.2s $ease-in-cubic;}
						.btn{background: url("../../images/club_house_sing_along/bg_item01_01.png") no-repeat 50% 50%;}
					}
					&.bg-color02{
						&:before{right: -7px;@include sprite($club_house_sing_along_bgitem1-bg_item02_02);transition: right 0.2s $ease-in-cubic;}
						.btn{background: url("../../images/club_house_sing_along/bg_item02_01.png") no-repeat 50% 50%;}
					}
					&.bg-color03{
						&:before{right: -15px;@include sprite($club_house_sing_along_bgitem1-bg_item03_02);transition: right 0.2s $ease-in-cubic;}
						.btn{background: url("../../images/club_house_sing_along/bg_item03_01.png") no-repeat 50% 50%;}
					}
					&.bg-color04{
						&:before{right: -12px;@include sprite($club_house_sing_along_bgitem1-bg_item04_02);transition: right 0.2s $ease-in-cubic;}
						.btn{background: url("../../images/club_house_sing_along/bg_item04_01.png") no-repeat 50% 50%;}
					}
					&.bg-color05{
						&:before{right: -11px;@include sprite($club_house_sing_along_bgitem1-bg_item05_02);transition: right 0.2s $ease-in-cubic;}
						.btn{background: url("../../images/club_house_sing_along/bg_item05_01.png") no-repeat 50% 50%;}
					}
					&.default{}
					&.disabled{
						.tit-week,
						.txt-info{opacity: 0.4;}
					}
					&.complete{}
					&.this-week{
						//&:before{content:"";position: absolute;top: -30px;right: -55px;width: 100px;height: 260px;animation: this-week-icon 1s steps(2) infinite forwards;background: url("../../images/sprite/club_house_reader_this_week.png") no-repeat 0 0;}
						&:after{content:"";position: absolute;top: -21px;right: -58px;width: 97px;height: 117px;
							animation: note-icon 0.6s steps(5) infinite forwards;background: url("../../images/sprite/club_house_sing_along_note1.png") no-repeat 0 0;
						}
						&:nth-of-type(1){&:before{right: -42px;}}
						&:nth-of-type(2){&:before{right: -40px;}}
						&:nth-of-type(3){&:before{right: -44px;}}
						&:nth-of-type(4){&:before{right: -50px;}}
						&:nth-of-type(5){&:before{right: -44px;}}
						.txt{
							&-info{color: #fff509;}
						}
						.icon{
							&-txt-week{width: 77px;height: 24px;background: url("../../images/club_house_reader/txt_week01_active.png") no-repeat 0 0;}
							&-num{
								&1{display: inline-block;@include sprite($club-house-reader-num-active-1);}
								&2{display: inline-block;@include sprite($club-house-reader-num-active-2);}
								&3{display: inline-block;@include sprite($club-house-reader-num-active-3);}
								&4{display: inline-block;@include sprite($club-house-reader-num-active-4);}
								&5{display: inline-block;@include sprite($club-house-reader-num-active-5);}
								&6{display: inline-block;@include sprite($club-house-reader-num-active-6);}
								&7{display: inline-block;@include sprite($club-house-reader-num-active-7);}
								&8{display: inline-block;@include sprite($club-house-reader-num-active-8);}
								&9{display: inline-block;@include sprite($club-house-reader-num-active-9);}
								&10{display: inline-block;@include sprite($club-house-reader-num-active-10);}
								&11{display: inline-block;@include sprite($club-house-reader-num-active-11);}
								&12{display: inline-block;@include sprite($club-house-reader-num-active-12);}
								&13{display: inline-block;@include sprite($club-house-reader-num-active-13);}
								&14{display: inline-block;@include sprite($club-house-reader-num-active-14);}
								&15{display: inline-block;@include sprite($club-house-reader-num-active-15);}
								&16{display: inline-block;@include sprite($club-house-reader-num-active-16);}
								&17{display: inline-block;@include sprite($club-house-reader-num-active-17);}
								&18{display: inline-block;@include sprite($club-house-reader-num-active-18);}
								&19{display: inline-block;@include sprite($club-house-reader-num-active-19);}
								&20{display: inline-block;@include sprite($club-house-reader-num-active-20);}
								&21{display: inline-block;@include sprite($club-house-reader-num-active-21);}
								&22{display: inline-block;@include sprite($club-house-reader-num-active-22);}
								&23{display: inline-block;@include sprite($club-house-reader-num-active-23);}
								&24{display: inline-block;@include sprite($club-house-reader-num-active-24);}
								&25{display: inline-block;@include sprite($club-house-reader-num-active-25);}
								&26{display: inline-block;@include sprite($club-house-reader-num-active-26);}
								&27{display: inline-block;@include sprite($club-house-reader-num-active-27);}
								&28{display: inline-block;@include sprite($club-house-reader-num-active-28);}
								&29{display: inline-block;@include sprite($club-house-reader-num-active-29);}
								&30{display: inline-block;@include sprite($club-house-reader-num-active-30);}
							}
						}
					}
					.btn{display: block;width: 100%;height: 100%;}
					.tit-week{position: absolute;top: 0;left: 0;width: 100%;padding-top: 8px;height: 24px;display: flex;align-content: center;justify-content: center;
						img{vertical-align: top;}
						.number-box{margin: 3px 0 0 10px;}
					}
					.img{position: absolute;top: 41px;left: 12px;overflow: hidden;width: 178px;height: 124px;border-radius: 14px;background-color: #fff;}
