							&14{display: inline-block;@include sprite($club-house-reader-num-14);}
							&15{display: inline-block;@include sprite($club-house-reader-num-15);}
							&16{display: inline-block;@include sprite($club-house-reader-num-16);}
							&17{display: inline-block;@include sprite($club-house-reader-num-17);}
							&18{display: inline-block;@include sprite($club-house-reader-num-18);}
							&19{display: inline-block;@include sprite($club-house-reader-num-19);}
							&20{display: inline-block;@include sprite($club-house-reader-num-20);}
							&21{display: inline-block;@include sprite($club-house-reader-num-21);}
							&22{display: inline-block;@include sprite($club-house-reader-num-22);}
