							&27{display: inline-block;@include sprite($club-house-reader-num-27);}
							&28{display: inline-block;@include sprite($club-house-reader-num-28);}
							&29{display: inline-block;@include sprite($club-house-reader-num-29);}
							&30{display: inline-block;@include sprite($club-house-reader-num-30);}
						}
					}
					.txt{
						&-box{position: absolute;bottom: 9px;left: 12px;height: 44px;display: flex;align-content: center;justify-content: center;flex-direction: column;}
